
export const zoom = {
  data() {
    return {
      loading: false,
      zoom: 1,
      screenWidth: null,
      screenHeight: null,
      chart: null,
      option: {}
    }
  },
  computed: {
    sidebar() {
      return this.$store.state.app.sidebar
    }
  },
  watch: {
    // 1024*600            （常见8、9寸电脑使用)
    // 1024*768            （常见10.4、12.1、14.1、15寸电脑使用）
    // 1280*1024            (常见14.1、15寸电脑使用）
    // 1280*800            （常见10.8、12.1、15.4寸电脑使用）
    // 1280*854            （不常见)
    // 1366*768            （常见15.2寸电脑使用)（主流）
    // 1440*1050            (常见15、16.1寸电脑使用)
    // 1440*900            （苹果17寸电脑)
    // 1600 * 900            (非主流)
    // 1600*1200            (常见15、16.1寸电脑使用)
    // 1600*1024            (不常见)
    // 1680*1050            (常见15.4、20寸电脑使见)
    // 1920*1080            (主流)
    // 1920*1200            (常见20寸电脑使见)
    screenWidth: {
      handler: function(newVal) {
        if (newVal) {
          if (newVal === 1024 || newVal < 1024) {
            this.zoom = 0.8
          }
          if (newVal === 1280) {
            this.zoom = 0.6
          }
          this.Adapter()
          this.curKey = true
        }
      },
      deep: true,
      immediate: true
    },
    sidebar: {
      handler: function(val) {
        setTimeout(() => {
          this.chart && this.chart.resize()
        }, 200)
      },
      deep: true
    }
  },
  methods: {
    resize() {
      window.addEventListener('resize', function() {
        this.screenWidth = window.screen.width || document.documentElement.clientWidth
        this.screenHeight = window.screen.height || document.documentElement.clientHeight
        this.chart && this.chart.resize()
      }.bind(this))
      this.Adapter()
    },
    Adapter() {
      // 1440*1050  1440*900
      const appWidth = document.getElementById('container') && document.getElementById('container').offsetWidth
      if (appWidth < 1500) {
        this.iconsWidth = '98%'
      } else {
        this.iconsWidth = (appWidth - 1500) * 0.3 + 460 + 'px'
      }
      if (
        this.screenWidth &&
        this.screenWidth < 1680 &&
        this.screenWidth > 1440
      ) {
        this.tablebottom1Class = 'tablebottom11'
      } else if (
        this.screenWidth &&
        this.screenWidth < 1441 &&
        this.screenWidth > 1380
      ) {
        this.tablebottom1Class = 'tablebottom11'
      } else if (
        this.screenWidth &&
        this.screenWidth < 1381 &&
        this.screenWidth > 1280
      ) {
        this.tablebottom1Class = 'tablebottom11'
      } else if (
        this.screenWidth &&
        this.screenWidth < 1281 &&
        this.screenWidth > 1024
      ) {
        this.tablebottom1Class = 'tablebottom11'
      } else if (this.screenWidth && this.screenWidth < 1025) {
        this.tablebottom1Class = 'tablebottom11'
      }
    }
  }
}
